@keyframes App-logo-spin {
  from {
    transform: rotate(-90deg);
  }
  50% {
    transform: rotate(90deg);
  }
  to {
    transform: rotate(-90deg);
  }
}

$ellipse: 250px;

//
$circle: $ellipse/12;
@mixin style($mod) {
  width: $mod * 2;
  height: $mod * 2;
  border-radius: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  position: absolute;
}

.ellipse {
  @include style($ellipse);
  border: 2px #555 solid;
  transform-style: preserve-3d;

  .circle {
    @include style($circle);
    /*background: deepskyblue;*/
    color: #02ffc2;
    font-size: 1.2rem;
    animation: move linear infinite;
  }
  @for $i from 1 through 12 {
    &:nth-child(#{$i}) {
      transform: rotateZ($i * 2 * 20deg) rotateY(70deg);
      .circle {
        animation-duration: $i + 3 + s;
      }
    }
  }
}

@keyframes move {
  from {
    transform: rotateZ(0) rotateX(0) translateX($ellipse) rotateZ(0) rotateX(0) rotateY(-70deg);
  }
  to {
    transform: rotateZ(360deg) rotateX(360deg) translateX($ellipse) rotateZ(-360deg) rotateX(-360deg) rotateY(-70deg);
  }
}

